import { useEffect, useRef, useState } from "react";
import Presenter from "./Presenter";
import { DEFAULT_TICKET, DURATION, EMPTY_ID, PRIZES } from "./constant";
import { fetchCodes, fetchConfigs } from "./services/code";
import { fetchTicketCodes } from "./services/firestore";
import { useSpinStore } from "./store/store";
import "./style.scss";
import { Config, StoredTicket, Ticket } from "./type";
import { flickerAnimate, getCurrentDate_Month } from "./utils";
import { SpinAudioHandle } from "../../components/common/AudioPlayer";
import { SingleSlotCounterRefProps } from "./components/PrizeEachItem";

const SpinGame = () => {
  const [value, setValue] = useState(DEFAULT_TICKET);
  const [btnDisabled, setBtnDisabled] = useState(false);
  const [showConfetti, setShowConfetti] = useState(false);
  const [isReseted, setIsReseted] = useState(false);
  const time = getCurrentDate_Month();
  const [numberOftickets, setNumberOftickets] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [spined, setSpined] = useState(false);
  const audioRef = useRef<SpinAudioHandle>(null);
  const winCarAudioRef = useRef<SpinAudioHandle>(null);
  const singleSlotCounterRef = useRef<SingleSlotCounterRefProps>();
  const [isShowCongratulation, setIsShowCongratulation] = useState(false);

  const {
    prizes,
    setPrizes,
    setTickets,
    currentPrize,
    setCurrentPrize,
    isLoggedIn,
    totalPrize,
    addTicketToPrize,
    availableTickets,
    removeTicketFromPrize,
    configs,
    setConfigs,
    popConfigTicket,
    setCongratulationTicket,
  } = useSpinStore((state) => state);
  const isDefaultPrize = currentPrize.id === EMPTY_ID;

  useEffect(() => {
    const promises = [fetchCodes(), fetchConfigs(), fetchTicketCodes()];

    Promise.all(promises).then(([tickets, configsResponse, prizeTickets]) => {
      let prizes = PRIZES;
      const ticketCodes: string[] = [];
      prizeTickets.forEach((tickets) => {
        let prizeId = "";
        tickets = tickets.map((ticket: StoredTicket) => {
          if (!prizeId) prizeId = ticket.prize_id;
          ticketCodes.push(ticket.ticket);
          return {
            fullName: ticket.user_name,
            userId: ticket.user_id,
            code: ticket.ticket,
          };
        });
        if (tickets.length === 0 || !prizeId) return;
        const prize = prizes.find((p) => p.id === prizeId);
        if (prize) prize.tickets = tickets;
      });
      const codes = (tickets as Ticket[]).filter(
        (c) => !ticketCodes.includes(c.code)
      );
      setConfigs(configsResponse as Config[]);
      setPrizes(prizes);
      setTickets(codes);
      setIsLoading(false);
    });
  }, []);

  useEffect(() => {
    if (isReseted) {
      setShowConfetti(false);
      if (currentPrize.singleSettedValue) {
        removeTicketFromPrize(currentPrize.singleSettedValue, currentPrize.id);
      } else {
        removeTicketFromPrize(value, currentPrize.id);
      }
      setValue(DEFAULT_TICKET);
      setCurrentPrize({ ...currentPrize, singleSettedValue: undefined });
    }
  }, [isReseted]);

  useEffect(() => {
    if (currentPrize.index > 0) {
      setNumberOftickets(getNumberOfTickets());
    }
  }, [currentPrize.tickets.length, currentPrize.index]);

  useEffect(() => {
    setShowConfetti(false);
    setValue(DEFAULT_TICKET);
  }, [currentPrize.index]);

  const getNumberOfTickets = () => {
    const total = currentPrize.numberOfTickets;
    const current = currentPrize.tickets.length;
    const val = total - current;
    return val > 0 ? val : 0;
  };

  const getNewValue = () => {
    const currentPrizeConfig = configs.find(
      (c) => c.index.toString() === currentPrize.id
    );
    if (currentPrizeConfig && currentPrizeConfig.codes.length > 0) {
      let counter = 0;
      while (counter <= currentPrizeConfig.codes.length) {
        const newTicket = currentPrizeConfig.codes[counter];
        popConfigTicket(currentPrize.id, newTicket);
        const newValue = availableTickets.find((t) => t.code === newTicket);
        if (newValue) {
          return newValue;
        }
        counter++;
      }
    }

    const codeLength = availableTickets.length;
    const randomIndex = Math.floor(Math.random() * codeLength);
    const newValue = availableTickets[randomIndex];

    return newValue;
  };

  const handleSpin = () => {
    if (!spined) {
      setSpined(true);
    }

    if (availableTickets.length === 0) {
      alert("Run out of tickets");
      return;
    }

    const newValue = getNewValue();

    setIsReseted(false);

    if (currentPrize.type === "single") {
      audioRef.current?.play();
      setTimeout(() => {
        audioRef.current?.pause();
      }, DURATION * 1000);

      setBtnDisabled(true);
      setTimeout(() => {
        setBtnDisabled(false);
      }, DURATION * 1000);
      if (currentPrize.singleSettedValue) {
        const isFinished = singleSlotCounterRef.current?.spin(currentPrize);

        if (isFinished) {
          setTimeout(() => {
            setCongratulationTicket(currentPrize.singleSettedValue);
            winCarAudioRef.current?.play();
            setTimeout(() => {
              addTicketToPrize(
                currentPrize.singleSettedValue!,
                currentPrize.id
              );
            }, 1000);
          }, DURATION * 1000 + 1000);
        }

        return;
      }

      const newCurrentPrize = { ...currentPrize };
      newCurrentPrize.singleSettedValue = newValue;
      setCurrentPrize(newCurrentPrize);
      singleSlotCounterRef.current?.spin(newCurrentPrize);
      return;
    }

    // handle for full-type prize
    audioRef.current?.play();
    setTimeout(() => {
      audioRef.current?.pause();
    }, DURATION * 1000 + 500);
    setValue(newValue);
    setShowConfetti(false);

    setTimeout(() => {
      setBtnDisabled(false);
      setShowConfetti(true);
      winCarAudioRef.current?.play();

      setTimeout(() => {
        addTicketToPrize(newValue, currentPrize.id);
        flickerAnimate("first_record");
      }, 1000);
    }, DURATION * 1000 + 1000);

    setTimeout(() => {
      audioRef.current?.pause();
    }, DURATION * 1000 + 500);
  };

  const getRandomValue = () => {
    const newValue = 100000 + Math.random() * 100000;

    return { userId: 0, fullName: "", code: newValue.toFixed(0).toString() };
  };

  const handleSpinDraft = () => {
    if (!spined) {
      setSpined(true);
    }
    const tickets = availableTickets.filter((t) => t.code !== value.code);
    const codeLength = tickets.length;
    const randomIndex = Math.floor(Math.random() * codeLength);
    const newValue = tickets[randomIndex] ?? getRandomValue();
    setValue(newValue);
    audioRef.current?.play();
    setIsReseted(false);
    setBtnDisabled(true);
    setTimeout(() => {
      setBtnDisabled(false);
    }, DURATION * 1000);
    setTimeout(() => {
      audioRef.current?.pause();
    }, DURATION * 1000 + 500);
  };

  const spin = () => {
    if (isLoading) return;
    if (isDefaultPrize) {
      handleSpinDraft();
    } else {
      handleSpin();
    }
  };

  const resetValue = () => {
    if (btnDisabled) return;
    setIsReseted(true);
  };

  const handleChangeGift = () => {
    const nextPrizeIndex = currentPrize.index + 1;
    if (nextPrizeIndex > totalPrize) return;

    const nextPrize = prizes.find((prize) => prize.index === nextPrizeIndex);

    if (nextPrize) {
      setCurrentPrize(nextPrize);
    }
  };

  return (
    <Presenter
      time={time}
      value={value}
      resetValue={resetValue}
      showConfetti={showConfetti}
      spin={spin}
      btnDisabled={btnDisabled}
      isReseted={isReseted}
      prize={currentPrize}
      handleChangeGift={handleChangeGift}
      numberOftickets={numberOftickets}
      spined={spined}
      isLoggedIn={isLoggedIn}
      isDefaultPrize={isDefaultPrize}
      audioRef={audioRef}
      singleSlotCounterRef={singleSlotCounterRef}
      isShowCongratulation={isShowCongratulation}
      winCarAudioRef={winCarAudioRef}
    />
  );
};

export default SpinGame;
