import { useEffect, useState } from "react";
import Image from "../../../components/common/Image";
import { useSpinStore } from "../store/store";
import { star } from "../utils";

export default function Records() {
  const { currentPrize, prizes, totalPrize } = useSpinStore((state) => state);
  const [prizeIndex, setPrizeIndex] = useState(0);
  const [currentPrizeLocal, setCurrentPrizeLocal] = useState(
    prizes[prizeIndex]
  );

  useEffect(() => {
    const prize = prizes.find((p) => p.index === prizeIndex);
    if (prize) setCurrentPrizeLocal(prize);
  }, [prizeIndex]);

  useEffect(() => {
    setPrizeIndex(currentPrize.index);
  }, [currentPrize.index, prizes]);

  const gotoPrevPrize = () => {
    if (prizeIndex - 1 < 1) return;
    setPrizeIndex(prizeIndex - 1);
  };

  const gotoNextPrize = () => {
    if (prizeIndex + 1 > totalPrize) return;
    setPrizeIndex(prizeIndex + 1);
  };

  if (currentPrize.index === 0) return <></>;
  return (
    <div className="flex flex-col gap-4 px-2 py-4 border border-[#005DB4] rounded text-white bg-[#002851cc] w-[250px]">
      <div className="flex items-center justify-between">
        <div className="flex items-center justify-center gap-2">
          <Image src={"/images/star.png"} alt="" className="h-[12px]" />
          <span className="text-white text[16px] font-bold">
            {currentPrizeLocal?.title}
          </span>
        </div>

        <div className="flex gap-2">
          <button
            className=" bg-[#0000004D] px-[10px] py-2 rounded"
            onClick={gotoPrevPrize}
          >
            <Image
              src={"/images/icons/arrow.svg"}
              className="rotate-180 h-[12px]"
              alt=""
            />
          </button>
          <button
            className=" bg-[#0000004D] px-[10px] py-2 rounded"
            onClick={gotoNextPrize}
          >
            <Image
              className="h-[12px]"
              src={"/images/icons/arrow.svg"}
              alt=""
            />
          </button>
        </div>
      </div>

      <div className="border border-[#005DB4] flex flex-col gap-4 p-3 bg-[#0000004D] rounded h-[80vh] overflow-scroll">
        {currentPrizeLocal?.tickets.map((ticket, index) => (
          <div key={index} id={index === 0 ? "first_record" : undefined}>
            <div className="flex gap-2 items-center">
              <Image src={"/images/avatar.png"} alt="" />
              <span className="font-bold">
                {star(ticket.fullName || "   ")}
              </span>
            </div>
            <div>
              <span>
                {"(TK)"} {ticket.code}
              </span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
