import { Config, Ticket } from "../type";

export const fetchCodes = async (): Promise<Ticket[]> => {
  const url = process.env.REACT_APP_CODE_URL || "";
  try {
    const response = await fetch(url).then((res) => {
      return res.json();
    });
    return response;
  } catch (error) {
    console.error("Error fetching and parsing CSV file", error);
    return [];
  }
};

