import Image from "../../../components/common/Image";
import Lottie from "lottie-react";
import ConfettiAnimation from "../../../assets/lotties/light-effect.json";
import { useSpinStore } from "../store/store";
import { useEffect, useState } from "react";

export default function Congratulation() {
  const { setCongratulationTicket, congratulationTicket, currentPrize } =
    useSpinStore((state) => state);

  const code = currentPrize.singleSettedValue?.code;

  const [showCloseIcon, setShowCloseIcon] = useState(false);

  useEffect(() => {
    if (congratulationTicket) {
      setTimeout(() => {
        setShowCloseIcon(true);
      }, 1000);
    } else setShowCloseIcon(false);
  }, [congratulationTicket]);

  const handleClose = () => {
    setCongratulationTicket(undefined);
  };

  return (
    <div
      className={`w-[100vw] h-[100vh] fixed top-0 left-0 bg-[#00000080] z-[100] ${
        congratulationTicket ? "" : "hidden"
      }`}
    >
      <div
        className={`fixed top-1/2 left-1/2 translate-x-[-50%] translate-y-[-50%] w-[700px] relative flex flex-col items-center congratulation-animate`}
      >
        <div
          className="w-full h-[332px] flex items-center justify-center congratulation-animate"
          style={{
            backgroundImage: "url('/images/congratulation.png')",
            backgroundSize: "contain",
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
          }}
        >
          <div>
            <div className="text-center">
              <span className="congratulation-animate-title text-[24px] text-white">
                {currentPrize.title}
              </span>
            </div>

            <div className="flex gap-4 mt-3">
              <div className="congratulation-animate-tk w-[68px] h-[68px] text-[36px] font-bold rounded-[8px] bg-[#FB923C] flex items-center justify-center">
                TK
              </div>

              {code &&
                code.split("").map((character, index) => (
                  <div
                    key={index}
                    className="congratulation-animate-item w-[68px] h-[68px] text-[48px] font-bold rounded-[8px] bg-white flex items-center justify-center"
                  >
                    {character}
                  </div>
                ))}
            </div>
          </div>
        </div>

        <div className="absolute top-1/2 left-1/2 translate-x-[-50%] translate-y-[-50%] z-[-1] w-[700px] h-[700px]">
          <Lottie animationData={ConfettiAnimation} loop={true} />
        </div>

        <button
          className={`absolute bottom-[-30px] w-[56px] h-[56px] bg-[#000000B3] rounded-full flex items-center justify-center ${
            showCloseIcon ? "" : "hidden"
          }`}
          onClick={handleClose}
        >
          <Image src={"/images/icons/close.svg"} />
        </button>
      </div>
    </div>
  );
}
