import { getImageSrc } from "../../utils/file";

type Props = {
  src: string;
  alt?: string;
  className?: string;
  id?: string;
};

const Image: React.FC<Props> = ({ src, alt, className, id }) => {
  return (
    <img id={id} src={getImageSrc(src)} alt={alt} className={className ?? ""} />
  );
};

export default Image;
