import { create } from "zustand";
import { PRIZES, PRIZE_SKELETON } from "../constant";
import {
  deleteTicketFromFirestore,
  pushTicketToFirestore,
} from "../services/firestore";
import { Config, Prize, StoredTicket, Ticket } from "../type";

interface StoreState {
  isLoggedIn: boolean;
  tickets: Ticket[];
  availableTickets: Ticket[];
  prizes: Prize[];
  totalPrize: number;
  currentPrize: Prize;
  configs: Config[];
  congratulationTicket?: Ticket;

  setIsLoggedIn: (isLoggedIn: boolean) => void;
  setCurrentPrize: (prize: Prize) => void;
  setTickets: (tickets: Ticket[]) => void;
  setPrizes: (prizes: Prize[]) => void;
  addTicketToPrize: (ticket: Ticket, prizeId: string) => void;
  removeTicketFromPrize: (ticket: Ticket, prizeId: string) => void;
  setConfigs: (configs: Config[]) => void;
  popConfigTicket: (prizeId: string, code: string) => void;
  setCongratulationTicket: (ticket?: Ticket) => void;
}

export const useSpinStore = create<StoreState>((set, get) => ({
  isLoggedIn: false,
  tickets: [],
  availableTickets: [],
  prizes: PRIZES,
  totalPrize: 0,
  currentPrize: PRIZE_SKELETON,
  congratulationTicket: undefined,
  configs: [],
  setIsLoggedIn: (isLoggedIn: boolean) => set({ isLoggedIn }),
  setCurrentPrize: (prize) => set({ currentPrize: prize }),
  setTickets: (tickets) => set({ tickets, availableTickets: tickets }),
  setPrizes: (prizes) => set({ prizes, totalPrize: prizes.length }),
  addTicketToPrize: (ticket: Ticket, prizeId: string) => {
    const { prizes, availableTickets } = get();
    const prize = prizes.find((p) => p.id === prizeId);
    if (prize) {
      const newAvailableTickets = availableTickets.filter(
        (t) => t.code !== ticket.code
      );
      if (prize.tickets) {
        prize.tickets.unshift(ticket);
      } else prize.tickets = [ticket];

      const newPrizes = prizes.map((p) => {
        if (p.id === prize.id) {
          return prize;
        }
        return p;
      });

      const newTicket: Omit<StoredTicket, "created_at"> = {
        user_name: ticket.fullName,
        user_id: ticket.userId,
        ticket: ticket.code,
        prize_id: prizeId,
        ...(prize.price && { price: prize.price }),
      };
      pushTicketToFirestore(newTicket, prizeId);

      set({ availableTickets: newAvailableTickets, prizes: newPrizes });
    }
  },
  removeTicketFromPrize: async (ticket: Ticket, prizeId: string) => {
    const { prizes, availableTickets } = get();

    const prize = prizes.find((p) => p.id === prizeId);

    if (prize) {
      prize.tickets = prize.tickets.filter((t) => t.code !== ticket.code);

      const newAvailableTickets = availableTickets.concat([ticket]);

      const newPrizes = prizes.map((p) => {
        if (p.id === prize.id) {
          return prize;
        }
        return p;
      });

      set({ availableTickets: newAvailableTickets, prizes: newPrizes });

      deleteTicketFromFirestore(ticket, prizeId);
    }
  },
  setConfigs: (configs) => set({ configs }),
  popConfigTicket: (prizeId, code) => {
    const { configs } = get();
    const newConfigs = configs.map((config) => {
      if (config.index.toString() === prizeId) {
        const newCodes = config.codes.filter((c) => c !== code);
        return {
          ...config,
          codes: newCodes,
        };
      }
      return config;
    });
  },
  setCongratulationTicket: (ticket) => set({ congratulationTicket: ticket }),
}));
