import React, { forwardRef, useImperativeHandle, useRef } from "react";
import { src } from "../../domains/lucky-draw/utils";

interface SpinAudioProps {
  src: string;
}

export interface SpinAudioHandle {
  play: () => void;
  pause: () => void;
}

const SpinAudio = forwardRef<SpinAudioHandle, SpinAudioProps>((props, ref) => {
  const audioRef = useRef<HTMLAudioElement>(new Audio(src(props.src)));

  useImperativeHandle(
    ref,
    () => ({
      play() {
        audioRef.current.currentTime = 0;
        audioRef.current.play();
      },
      pause() {
        audioRef.current.pause();
      },
    }),
    []
  );

  return null;
});

export default SpinAudio;
