import { MONTHS } from "./constant";

// return { month: string, date: number}
export const getCurrentDate_Month = () => {
  const date = new Date();
  const month = date.getMonth();

  return {
    month: MONTHS[month],
    date: date.getDate(),
  };
};

export const src = (href: string) => {
  return process.env.PUBLIC_URL + href;
};

export const getDummyCharacters = () => {
  const characters = "0123456789abcdefghijklmnopqrstuvwxyz";
  return characters.toUpperCase().split("");
};

export const animate = (id: string, callback?: Function) => {
  const imageEle = document.getElementById(id);
  if (imageEle) {
    imageEle.classList.add("prize-animate-end");
    imageEle.classList.remove("prize-animate-start");
    setTimeout(() => {
      callback && callback();
      imageEle.classList.remove("prize-animate-end");
      imageEle.classList.add("prize-animate-start");
    }, 800);
  }
};

export const flickerAnimate = (id: string) => {
  const ele = document.getElementById(id);
  if (ele) {
    ele.classList.add("flicker-animate");
    setTimeout(() => {
      ele.classList.remove("flicker-animate");
    }, 1000);
  }
};

export const star = (str: string) => {
  return str.replace(/.{3}$/, "***");
};
