import { useState } from "react";
import { useSpinStore } from "../store/store";
import { NODE_ENV } from "../constant";

export default function Auth() {
  const [password, setPassword] = useState(
    process.env.REACT_APP_NODE_ENV === NODE_ENV.DEV
      ? process.env.REACT_APP_PASSWORD || ""
      : ""
  );
  const { setIsLoggedIn } = useSpinStore((state) => state);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPassword(e.target.value);
  };

  const handleSubmit = () => {
    if (password === process.env.REACT_APP_PASSWORD) {
      setIsLoggedIn && setIsLoggedIn(true);
    }
  };

  return (
    <div className="absolute w-[400px] min-h-[200px] shadow-md rounded bg-white top-1/2 left-1/2 translate-x-[-50%] translate-y-[-50%] p-4">
      <div className="text-xl font-bold text-black text-center uppercase">
        <span>Login</span>
      </div>

      <div className="mt-6 flex flex-col gap-4">
        <div>
          <input
            type="password"
            className="border rounded outline-none w-full h-[40px] px-4 text-center"
            placeholder="Enter the password"
            value={password}
            onChange={handleChange}
          />
        </div>

        <div>
          <button
            className="rounded bg-[#273c75] text-white w-full h-[40px]"
            onClick={handleSubmit}
          >
            Summit
          </button>
        </div>
      </div>
    </div>
  );
}
