import SlotCounter from "@tuanngocptn/react-slot-counter";
import Lottie from "lottie-react";
import ConfettiAnimation from "../../assets/lotties/confetti.json";
import Image from "../../components/common/Image";
import Auth from "./components/Auth";
import PrizeInformation from "./components/PrizeInformation";
import Records from "./components/Records";
import SEO from "./components/SEO";
import { DEFAULT_TICKET, DURATION } from "./constant";
import { Prize, Ticket } from "./type";
import { getDummyCharacters } from "./utils";
import SpinAudio from "../../components/common/AudioPlayer";
import SingleSlotCounter from "./components/PrizeEachItem";
import Congratulation from "./components/Congratulation";

export type Props = {
  time: { month: string; date: number };
  showConfetti: boolean;
  value: Ticket;
  btnDisabled: boolean;
  spin: () => void;
  resetValue: () => void;
  isReseted: boolean;
  prize: Prize;
  handleChangeGift: () => void;
  numberOftickets: number;
  spined: boolean;
  isLoggedIn: boolean;
  isDefaultPrize: boolean;
  audioRef: any;
  singleSlotCounterRef: any;
  isShowCongratulation: boolean;
  winCarAudioRef: any;
};

export default function Presenter({
  time,
  showConfetti,
  value,
  btnDisabled,
  spin,
  resetValue,
  isReseted,
  prize,
  handleChangeGift,
  numberOftickets,
  spined,
  isLoggedIn,
  isDefaultPrize,
  audioRef,
  singleSlotCounterRef,
  isShowCongratulation,
  winCarAudioRef,
}: Props) {
  return (
    <>
      <SEO />
      {/* BACKGROUND COVER */}
      <div className="absolute w-[100vw] h-[100vh] z-[-1]">
        <Image
          src={"/images/background.png"}
          alt=""
          className="object-cover w-full h-full"
        />
      </div>
      {/* END BACKGROUND COVER */}

      {!isLoggedIn && <Auth />}

      {isLoggedIn && (
        <>
          <div className="w-full h-full min-h-[100vh] min-w-[100vw] flex items-center justify-center relative _container">
            <div className="fixed right-[50px] top-0">
              <div className="h-[114px] w-[115px] border-[2px] border-[#FB923C] border-t-0 bg-[#000] flex flex-col items-center">
                <span className="text-[48px] text-white font-bold">
                  {time.date}
                </span>
                <span className="text-white text-[20px]">{time.month}</span>
              </div>
            </div>

            <div className="relative">
              <div className="w-full absolute max-w-[500px] top-1/2 left-1/2 z-[100] translate-x-[-50%] translate-y-[-50%] pointer-events-none">
                {showConfetti && (
                  <Lottie animationData={ConfettiAnimation} loop={true} />
                )}
              </div>
              <div className="zoom_container">
                {/* PRIZE SECTION */}
                <div className="relative min-w-[100vw]">
                  <div className="w-full flex justify-center">
                    <div className="flex flex-col justify-center items-center">
                      <PrizeInformation gift={prize} />
                      <div className="z-0">
                        <Image
                          src={"/images/shape1.png"}
                          className="w-[620px]"
                          alt=""
                        />
                      </div>
                    </div>
                  </div>
                </div>
                {/*END PRIZE SECTION */}

                <div className="w-full flex justify-center mt-4 z-10 relative">
                  <div className="bg-[#007bff] spin-container px-10 py-10 rounded-[50px] relative">
                    <div className="border-[4px] border-[#1A91FF] rounded-[20px] mt-[-130px] relative">
                      <div className="absolute top-1/2 translate-y-[-50%] left-[-40px]">
                        <Image
                          src={"/images/shape4.png"}
                          className="w-[36px]"
                          alt=""
                        />
                      </div>
                      <div className="px-[30px] py-[10px] bg-white rounded border-[4px] border-black rounded-[16px] overflow-hidden flex items-center gap-2">
                        <div className="h-[120px] prefix-bg text-white flex items-center w-[96px] rounded-l-[20px] rounded-r-[8px] justify-center ">
                          <span className="text-white text-[36px] font-bold">
                            TK
                          </span>
                        </div>
                        {prize.type === "full" ? (
                          <SlotCounter
                            value={value.code}
                            duration={DURATION}
                            dummyCharacters={getDummyCharacters()}
                            containerClassName="counter-container"
                            charClassName="counter-char"
                            numberClassName="counter-number text-white"
                            direction={
                              value.code === DEFAULT_TICKET.code
                                ? "bottom-up"
                                : "top-down"
                            }
                            autoAnimationStart={false}
                            hasInfiniteList={true}
                            animateUnchanged={true}
                          />
                        ) : (
                          <SingleSlotCounter ref={singleSlotCounterRef} />
                        )}
                      </div>
                      <div className="absolute top-1/2 translate-y-[-50%] right-[-40px]">
                        <Image
                          src={"/images/shape4.png"}
                          className="w-[36px] rotate-180"
                          alt=""
                        />
                      </div>

                      {/* LEVER SECTION */}

                      <div
                        className={`absolute right-[-80px] z-[10] top-[-110px] ${
                          spined
                            ? btnDisabled
                              ? "lever-t-top-to-bottom-disappear-animate"
                              : "lever-t-bottom-to-top-appear-animate"
                            : ""
                        }`}
                        id="lever"
                      >
                        <Image src={"/images/lever.png"} className="" alt="" />
                      </div>

                      <div
                        className={`absolute top-[10px] right-[-80px] z-[10] ${
                          spined
                            ? btnDisabled
                              ? "lever-b-top-to-bottom-appear-animate"
                              : "lever-b-bottom-to-top-disappear-animate"
                            : "hidden"
                        }`}
                        id="lever"
                      >
                        <Image
                          src={"/images/lever.png"}
                          className="rotate-[160deg]"
                          alt=""
                        />
                      </div>
                    </div>
                    <div className="mt-6 w-full flex justify-center gap-4 items-center px-10">
                      {/* RESET GIFT BUTTON */}
                      <button
                        className="bg-[#0028514D] rounded px-5 h-[70px]"
                        onClick={resetValue}
                      >
                        <Image src={"/images/icons/reset.svg"} alt="" />
                      </button>

                      {/* SPIN BUTTON */}
                      <button
                        disabled={btnDisabled}
                        onClick={spin}
                        className={`rounded w-[90%] py-4 text-[36px] ${
                          btnDisabled
                            ? "bg-[#2355AD] text-slate-400 cursor-wait"
                            : "bg-[#002851] text-white cursor-pointer"
                        } `}
                      >
                        SPIN
                      </button>

                      {/* NEXT GIFT BUTTON */}
                      <button
                        className="bg-[#0028514D] rounded px-6 h-[70px]"
                        onClick={handleChangeGift}
                      >
                        <Image src={"/images/icons/arrow.svg"} alt="" />
                      </button>
                    </div>

                    {/* TICKET COUNTER */}
                    {!isDefaultPrize && (
                      <div className="absolute bottom-[30px] right-[-110px] w-[100px] z-[-1]">
                        <div className="relative">
                          <Image src={"/images/quantity-bg.png"} alt="" />
                          <div className="absolute top-1/2 left-1/2 translate-x-[-60%] translate-y-[-50%]">
                            <span className="text-[#FB923C] text-[36px] font-bold">
                              {numberOftickets}
                            </span>
                          </div>
                        </div>
                      </div>
                    )}
                    {/* END TICKET COUNTER */}
                  </div>
                </div>

                <div className="w-full flex justify-center mt-6">
                  <Image
                    className="w-[277px]"
                    src={"/images/logo.png"}
                    alt=""
                  />
                </div>
              </div>
            </div>
          </div>

          <div className="fixed top-[10vh] left-[30px]">
            <Records />
          </div>

          <SpinAudio
            ref={audioRef}
            src="/audios/mixkit-bike-wheel-spinning.wav"
          />

          <SpinAudio ref={winCarAudioRef} src="/audios/fairy-win.mp3" />

          <Congratulation />
        </>
      )}
    </>
  );
}
