import SlotCounter from "@tuanngocptn/react-slot-counter";
import { forwardRef, useEffect, useImperativeHandle, useState } from "react";
import { DEFAULT_TICKET, DURATION } from "../constant";
import { getDummyCharacters } from "../utils";
import { useSpinStore } from "../store/store";
import { Prize } from "../type";

export interface SingleSlotCounterRefProps {
  spin: (prize: Prize) => boolean;
}

const SingleSlotCounter = forwardRef<SingleSlotCounterRefProps>((_, ref) => {
  const { currentPrize } = useSpinStore((state) => state);
  const [currentSlot, setCurrentSlot] = useState(0);
  const [values, setValues] = useState(DEFAULT_TICKET.code.split(""));

  useEffect(() => {
    if (!currentPrize.singleSettedValue) {
      setValues(DEFAULT_TICKET.code.split(""));
      setCurrentSlot(0);
    }
  }, [currentPrize.singleSettedValue]);
 
  useImperativeHandle(
    ref,
    () => ({
      spin: (prize) => {
        if (!prize.singleSettedValue || currentSlot > values.length - 1) {
          return false;
        }

        const currentValue = prize.singleSettedValue.code[currentSlot];
        const newValues = [...values];
        newValues[currentSlot] = currentValue;

        setValues(newValues);
        setCurrentSlot(currentSlot + 1);
        return currentSlot === values.length - 1;
      },
    }),
    [currentSlot]
  );

  return (
    <div className="flex ">
      {values &&
        values.length > 0 &&
        values.map((val, index) => (
          <div key={index}>
            <SlotCounter
              value={val}
              duration={DURATION}
              dummyCharacters={getDummyCharacters()}
              containerClassName="counter-container"
              charClassName="counter-char"
              numberClassName="counter-number text-white"
              direction={
                val !== DEFAULT_TICKET.code[0] ? "top-down" : "bottom-up"
              }
              autoAnimationStart={false}
              hasInfiniteList={true}
              animateUnchanged={true}
            />
          </div>
        ))}
    </div>
  );
});

export default SingleSlotCounter;
