import { useEffect, useState } from "react";
import Image from "../../../components/common/Image";
import { Prize } from "../type";
import { animate } from "../utils";
import { EMPTY_ID } from "../constant";

export default function PrizeInformation({ gift }: { gift: Prize }) {
  const [localGift, setLocalGift] = useState(gift);
  const isDefaultGift = gift.id === EMPTY_ID;

  useEffect(() => {
    if (gift.index === 0) return;
    const callback = () => {
      setLocalGift(gift);
    };
    animate("prize-info-title");
    animate("prize-info-desc");
    animate("prize_image", callback);
  }, [gift.id]);

  return (
    <>
      <div
        className="mb-[-70px] z-10 flex flex-col items-center gap-6"
        id="prize_image"
      >
        {isDefaultGift && <div className="invisible w-[1px] h-[44px]"></div>}
        {!isDefaultGift && (
          <Image src={localGift.titleImage} className="h-[44px]" />
        )}
        <Image
          src={localGift.image}
          alt=""
          className={`${isDefaultGift ? "ml-20" : ""} h-[260px]`}
        />
      </div>
      <div className="absolute bottom-[100px] left-1/2 translate-x-[-50%] text-center text-white z-10">
        <div id="prize-info-title">
          <span className="text-[44px] italic">{localGift.prize}</span>
        </div>
        <div id="prize-info-desc">
          <span className="text-[20px]">{localGift.info}</span>
        </div>
      </div>
    </>
  );
}
