import {
  addDoc,
  collection,
  deleteDoc,
  getDocs,
  limit,
  query,
  where,
  serverTimestamp,
  orderBy,
} from "firebase/firestore";
import { StoredTicket, Ticket } from "../type";
import { db } from "../../../services/firestore/provider";
import { WIN_CARS_PRIZES_COLLECTION_PATH } from "../../../services/firestore/config";

export const pushTicketToFirestore = (
  ticket: Omit<StoredTicket, "created_at">,
  prizeId: string
) => {
  addDoc(collection(db, `${WIN_CARS_PRIZES_COLLECTION_PATH}/${prizeId}`), {
    ...ticket,
    created_at: serverTimestamp(),
  });
};

export const deleteTicketFromFirestore = async (
  ticket: Ticket,
  prizeId: string
) => {
  try {
    const collectionName = `${WIN_CARS_PRIZES_COLLECTION_PATH}/${prizeId}`;
    const q = query(
      collection(db, collectionName),
      where("ticket", "==", ticket.code),
      limit(1)
    );
    const querySnapshot = await getDocs(q);

    if (!querySnapshot.empty) {
      const docRef = querySnapshot.docs[0].ref;
      await deleteDoc(docRef);
    }
  } catch (_) {}
};

export const fetchTicketCodes = async () => {
  const prizeIds = ["0", "1", "2", "3", "4"];

  const prizeTickets: any[] = [];

  for (let i = 0; i < prizeIds.length; i++) {
    const q = query(
      collection(db, `${WIN_CARS_PRIZES_COLLECTION_PATH}/${prizeIds[i]}`),
      orderBy("created_at", "desc")
    );
    const querySnapshot = await getDocs(q);

    const documents: any[] = [];
    querySnapshot.forEach((doc) => {
      documents.push({ id: doc.id, ...doc.data() });
    });

    prizeTickets.push(documents);
  }
  return prizeTickets;
};
